.slider {
  position: relative;

  .bg {
    width: 100%;
  }
}

.slides {
  position: absolute;
  z-index: 9;
  margin: 26px 0 0 24px;

  .slide {
    position: relative;
    height: 90px;
    cursor: pointer;
    margin-bottom: 7px;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.3);
      z-index: 9;
      pointer-events: none;
    }

    &.active {
      &::before {
        content: none;
      }
    }
  }

  img {
    height: 90px;
    cursor: pointer;
    position: relative;
  }
}