$columns: 12;
$atom: (100% / $columns);

$break_lmd: 1480px;
$break_md: 940px;
$break_sm: 780px;
$break_xxs: 440px;

$offset: 16px;
$offset_lmd: $offset;
$offset_md: $offset;
$offset_sm: $offset;
$offset_xxs: $offset;

$offset_one_side: ($offset / 2);
$offset_lmd_one_side: ($offset_lmd / 2);
$offset_md_one_side: ($offset_md / 2);
$offset_sm_one_side: ($offset_sm / 2);
$offset_xxs_one_side: ($offset_xxs / 2);

$fields: 0px;
$fields_lmd: 40px;
$fields_md: 15px;
$fields_sm: 10px;
$fields_xxs: 10px;

@mixin wrapper-full(){
    padding-left: $fields;
    padding-right: $fields;

    @include lmd-block(){
        padding-left: $fields_lmd;
        padding-right: $fields_lmd;
    }

    @include md-block(){
        padding-left: $fields_md;
        padding-right: $fields_md;
    }

    @include sm-block(){
        padding-left: $fields_sm;
        padding-right: $fields_sm;
    }

    @include xxs-block(){
        padding-left: $fields_xxs;
        padding-right: $fields_xxs;
    }
}

@mixin row-offsets(){
    margin-left: ($offset_one_side * -1);
    margin-right: ($offset_one_side * -1);
}

@mixin col-offsets($type){
    #{$type}-left: $offset_one_side;
    #{$type}-right: $offset_one_side;
}

@mixin wrapper(){
    max-width: 1390px;
    margin: 0 auto;
    @include wrapper-full();
}

@mixin row-flex(){
    display: flex;
    flex-wrap: wrap;
    @include row-offsets();
}

@mixin row-float(){
    @include row-offsets();
    @include clearfix();
}

@mixin row-ib(){
    @include row-offsets();
}

@mixin col(){
    box-sizing: border-box;
    word-wrap: break-word;
    @include col-offsets(margin);
}

@mixin col-float(){
    float: left;
}

@mixin col-ib(){
    display: inline-block;
    vertical-align: top;
}

@mixin col-padding(){
    word-wrap: break-word;
    @include col-offsets(padding);
}

@mixin size($n){
    $val: 100% / $columns * $n;
    width: #{calc(#{$val} - #{$offset})};
}

@mixin size-lmd($n){
    $val: 100% / $columns * $n;
    @include lmd(width, #{calc(#{$val} - #{$offset_lmd})});
}

@mixin size-md($n){
    $val: 100% / $columns * $n;
    @include md(width, #{calc(#{$val} - #{$offset_md})});
}

@mixin size-sm($n){
    $val: 100% / $columns * $n;
    @include sm(width, #{calc(#{$val} - #{$offset_sm})});
}

@mixin size-xxs($n){
    $val: 100% / $columns * $n;
    @include xxs(width, #{calc(#{$val} - #{$offset_xxs})});
}

@mixin shift($n){
    $val: 100% / $columns * $n;
    margin-left: #{calc(#{$val} + #{$offset_one_side})};
    margin-right: #{calc(#{$val} + #{$offset_one_side})};
}

@mixin shift-left($n){
