@mixin lmd($name, $value){
    @media screen and (max-width: $break_lmd){
        #{$name}: $value;
    }
}

@mixin lmd-block(){
    @media screen and (max-width: $break_lmd){
        @content;
    }
}

@mixin md($name, $value){
    @media screen and (max-width: $break_md){
        #{$name}: $value;
    }
}

@mixin md-block(){
    @media screen and (max-width: $break_md){
        @content;
    }
}

@mixin sm($name, $value){
